// Variables
@import 'variables/layout';
@import 'variables/colour';
@import 'variables/font';

// Mixins
@import 'mixins/animation';
@import 'mixins/gradients';
@import 'mixins/styles';

.container{
	@include container;
}
.row {
	@include resetFontSize;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	margin-left: -$spacing-grid;
	margin-right: -$spacing-grid;
	box-sizing: border-box;
	&::before{display: none}
	&::after{display: none}
}
.col {
	@include resetFontSize;
	margin: 0 $spacing-grid;
	box-sizing: border-box;
	header,section,footer,div,span{display: block;}
	&.c1 {
		flex-grow: 1;
		flex-shrink: 11;
		flex-basis: $one;
		width: $one;
	}
	&.c2 {
		flex-grow: 2;
		flex-shrink: 10;
		flex-basis: $two;
		width: $two;
	}
	&.c3 {
		flex-grow: 3;
		flex-shrink: 9;
		flex-basis: $three;
		width: $three;
	}
	&.c4 {
		flex-grow: 4;
		flex-shrink: 8;
		flex-basis: $four;
		width: $four;
	}
	&.c5 {
		flex-grow: 5;
		flex-shrink: 7;
		flex-basis: $five;
		width: $five;
	}
	&.c6 {
		flex-grow: 6;
		flex-shrink: 6;
		flex-basis: $six;
		width: $six;
	}
	&.c7 {
		flex-grow: 7;
		flex-shrink: 5;
		flex-basis: $seven;
		width: $seven;
	}
	&.c8 {
		flex-grow: 8;
		flex-shrink: 4;
		flex-basis: $eight;
		width: $eight;
	}
	&.c9 {
		flex-grow: 9;
		flex-shrink: 3;
		flex-basis: $nine;
		width: $nine;
	}
	&.c10 {
		flex-grow: 10;
		flex-shrink: 2;
		flex-basis: $ten;
		width: $ten;
	}
	&.c11 {
		flex-grow: 11;
		flex-shrink: 1;
		flex-basis: $eleven;
		width: $eleven;
	}
	&.c12 {
		flex-grow: 12;
		flex-shrink: 0;
		flex-basis: $twelve;
		width: $twelve;
	}
}

