// Unit of Measurement
$unit: 				0.6rem;  	// 1rem = 10px
$spacing-small: 	$unit;  		// 6px
$spacing-medium: 	$unit * 3;  // 18px
$spacing-large: 	$unit * 5;	// 30px

$sidebar-width: 396px;
$content-width: calc(100% - 396px);

$sidebar-timeline-width: 150px;
$content-timeline-width: calc(100% - 150px);

// Breakpoints
$minbody:		91.8rem;
$maxbody:		$minbody + $spacing-medium * 2;
$tablet:			86.0rem;
$mobile:			54.0rem;

$line-length:	750px;


// Column Width Value [ MOVING TO CSS GRID ]
$spacing-grid: 	$spacing-medium / 2;  // 9px
$one:       6.0rem;
$two:       13.8rem;
$three:     21.6rem;
$four:      29.4rem;
$five:      37.2rem;
$six:       45.0rem;
$seven:     52.8rem;
$eight:     60.6rem;
$nine:      68.4rem;
$ten:       76.2rem;
$eleven:    84.0rem;
$twelve:    91.8rem;

