// Variables
@import 'variables/layout';
@import 'variables/colour';
@import 'variables/font';


h1,h2,h3,h4,h5,h6{
	font-family: 'Oswald', sans-serif;
	text-transform: uppercase;
	&.highlight{
		color: $brand !important
	}
}


// Form
h1 {
	font-size: 				$unit * 7;
	line-height: 			$unit * 9;
	font-weight: 			$font-weight-bold;
	color: 					$text-colour;
	margin-bottom: 		.1em;
}
h2 {
	font-size: 				$unit * 6;
	line-height: 			$unit * 8;
	font-weight: 			$font-weight-medium;
	color: 					$text-colour;
	margin-bottom: 		.2em;
}
h3 {
	font-size: 				$unit * 5;
	line-height: 			$unit * 7;
	font-weight: 			$font-weight-bold;
	color: 					$text-colour;
	margin-bottom: 		.3em;
}
h4 {
	font-size: 				$unit * 4;
	line-height: 			$unit * 6;
	font-weight: 			$font-weight-medium;
	color: 					$text-colour;
	margin-bottom: 		.4em;
}
h5 {
	font-size: 				$unit * 3.5;
	line-height: 			$unit * 5.5;
	font-weight: 			$font-weight-bold;
	color: 					$text-colour;
	margin-bottom: 		.5em;
}
h6 {
	font-size: 				$unit * 2;
	line-height: 			$unit * 4;
	font-weight: 			$font-weight-medium;
	color: 					transparentize($text-colour, .3);
	margin-bottom: 		.6em;
}
p, li {
	font-size: 				$unit * 3;
	line-height: 			$unit * 5;
	font-weight: 			$font-weight-regular;
	color: 					transparentize($text-colour, .3);
	margin-bottom: 		.5em;
	a{
		color: $link-colour-before;
		&:hover{
			color: $link-colour-after
		}
	}
	&.highlight{
		color: $brand-alt !important
	}
}



// Spacing
h1, h2, h3, h4, h5, h6, p{
	&:last-child{
		margin-bottom:0;
	}
}




// Sub Styles
a {
	text-decoration: 	none;
	display: 					inline-block;
	color: 						inherit;
}
a:hover {color: 		inherit;}
em{font-style: italic;}
strong,b{
	font-weight: $font-weight-medium;
	color: $text-colour;
	&h1,&h2,&h3,&h4,&h5,&h6{
		font-weight: $font-weight-bold;
		color: $brand
	}
}



// Images
img{
	display: inline-block;
}
figure{
	display: 				inline-block;
	width: 					100%;
	margin: 					$unit * 5 auto;
	text-align: 			center;
	img{
		padding: 			0;
		margin: 				0 auto;
		box-sizing: 		border-box;
		max-width: 			100%;
		height: 				auto;
	}
}
figcaption{
	display: 				block;
	margin: 					0 auto;
	text-align:				center;
	padding: 				$unit * 3;
	color: 					$text-colour;
}



// Quotes
blockquote{
	position: relative;
	margin: $unit * 5 0;
	padding: $unit * 3;
	border-style: solid;
	border-width: 1px 1px 1px 4px;
	border-color: $brand;
	border-left-color: $brand;
	strong{color: $brand}
}



// Lists
article,.typography{
	ul,ol{
		margin: $spacing-medium;
		ul,ol{
			margin: $spacing-small $spacing-medium;
		}
		li{
			//padding: 			$unit 0;
			margin-left: 				$spacing-medium;
			margin-right: 				-$spacing-medium * 2;
			max-width: 			100%;
		}
		li:before{
			float: left;
			margin-right: $spacing-small;
			opacity: .6;
		}
		h1, h2, h3, h4, h5, h6, p{
			display: inline-block;
		}
	}

	ul{
		list-style: disc;
		list-style-position: inside;
		li{list-style-type: disc}
		ol{
			list-style: none;
			li{list-style-type: none}
		}
	}

	ol{
		counter-reset: parent-counter;
		li{counter-increment: parent-counter}
		li:before{content: counter(parent-counter)"."}
		ol{
			counter-reset: child-counter;
			li{counter-increment: child-counter}
			li:before{content: counter(child-counter)"."}
		}
		ul{
			counter-reset: no-counter;
			li{counter-increment: no-counter}
			li:before{content: none}
		}
	}
}