// Variables
@import '../_framework/variables/layout';
@import '../_framework/variables/colour';
@import '../_framework/variables/font';

// Mixins
@import '../_framework/mixins/animation';

#timeline{
	ul,li,h1,h2,h3,h4,h5,h6,p{
		max-width: 100%;
	}
}

.timeline-section{
	display: flex;
	margin-bottom: 0 !important;
}




///
/// SIDEBAR
///
.path{
	.path-top,.path-bottom{
		width: 3px;
		margin: 0 15px;
		background-color: $light;
	}
	.path-top{
		flex-grow: 0;
		flex-shrink: 0;
		height: $unit * 10;
	}
	.path-bottom{
		flex-grow: 1;
		flex-shrink: 1;
	}
	
	.path-anchor,.path-anchor-edge,.path-anchor-mid{
		@include defaultAnimation (.6s)
	}
	.path-anchor{
		width: 33px;
		height: 33px;
		border-radius: 1000px;
		//background-color: rgba(255,255,255,.12);
		flex-grow: 0;
		flex-shrink: 0;
		position: relative;
		transform: rotateZ(180deg);
		.path-anchor-mid{
			width: 7px;
			height: 7px;
			background-color: white;
			border-radius: 1000px;
			margin: 13px;
		}
		.path-anchor-edge{
			width: 12px;
			height: 12px;
			background-color: $light;
			position: absolute;
			top: 12px;
			bottom: 12px;
			left: calc(100% - 7px);
			transform: rotateZ(45deg);
			//clip-path: circle(50% at 50% 50%);
			mask: url(src/clip-timeline.png);
			mask-size: 33px 33px;
			mask-position: -22px 0px;
			mask-repeat: no-repeat;
		}
	}
}

.timeline-section-sidebar{
	@include resetFontSize;
	flex-basis: $sidebar-timeline-width;
	flex-grow: 0;
	flex-shrink: 0;
	text-align: right;
	padding-right: $unit * 3;
	display: flex;
	h1,h2,h3,h4,h5,h6,p{
		margin-bottom: $unit;
	}
	.text{
		flex-grow: 1;
		flex-shrink: 1;
		padding-right: $unit * 2;
		padding-top: $unit * 10;
	}
	.path{
		flex-grow: 0;
		flex-shrink: 0;
		display: flex;
		flex-direction: column;
	}
	&.past,&.now{
		.path-top,.path-anchor-mid{
			background-color: $brand;
		}
		h6{
			color: $brand;
		}
	}
	&.past{
		.path-bottom{
			background-color: $brand;
		}
	}
	&.later{
		.path-anchor-mid{
			opacity: .3;
		}
		.text{
			opacity: .42;
		}
		.path-top{
			@include gradient(transparentize($light, .66), $light, transparentize($light, .66));
		}
		.path-bottom{
			@include gradient(transparent, transparentize($light, .66), transparent);
		}
	}
}




///
/// CONTENT
///
.timeline-section-entries{
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: $content-timeline-width;
}

.timeline-section{
	&:hover{
		.path-anchor-edge,.path-anchor-mid{
			background-color: $brand;
		}
		.path-anchor{
			transform: rotateZ(0deg)
		}
	}
	&:last-child{
		.timeline-section-entry{
			background-color: transparent;
			padding: 0;
		}
	}
}

.timeline-section-entry{
	margin-top: $unit * 5;
	margin-bottom: $unit * 5;
	padding-top: $unit * 5;
	//background-image: linear-gradient(-153deg, #1F1F1F 0%, #171717 95%);
	background-color: lighten($dark, 6);
	border-radius: $unit;
	overflow: hidden;
	&:first-child{
		margin-top: $unit * 10;
	}
	&:last-child{
		margin-bottom: $unit * 10;
	}
	h1,h2,h3,h4,h5,h6,p,li{
		padding: 0 $unit * 5;
	}
	h1,h2,h3,h4,h5,p{
		margin-bottom: $unit * 3;
	}
	&.special{
		padding: $unit * 2 $unit * 3 !important;
		background-color: transparentize($brand-alt, .3) !important;
		background-image: none;
		text-align: center;
		h1,h2,h3,h4,h5,h6,p{
			color: white;
			opacity: 1;
			margin-bottom: 0 !important;
			font-size: $unit * 3;
			line-height: $unit * 5;
		}
	}
}

.timeline-section-entry-features{
	strong,b{
		//background-color: rgba(255, 255, 255, .12);
		//padding: $unit $unit * 1.5;
		border-radius: $unit;
		font-size: 1.5rem;
		margin-right: $unit;
	}
	li{
		position: relative;
		margin: 0 !important;
		//background-color: transparentize(white,.99);
		padding-top: $unit * 2;
		padding-bottom: $unit * 2;
		border-top: 1px solid transparentize(white,.92);
		border-bottom: 1px solid transparentize(black,.92);
		&:nth-child(odd){
			background-color: transparentize(white,.96);
			border-top: 1px solid transparentize(white,.96);
			border-bottom: 1px solid transparentize(black,.96);
		}
	}
	.new-feature,.new-map,.new-cosmetics,.new-mode,.esports{
		&:before{
			background-color: $brand;
			content: "";
			width: 6px;
			height: 6px;
			border-radius: 100px;
			position: absolute;
			display: block;
			left: $unit * 2;
			top: $unit * 4;
		}
	}
	.new-feature{
		&:before{
			background-color: lighten(blue,20);
		}
	}
	.new-cosmetics{
		&:before{
			background-color: lighten(green,20);
		}
	}
	.new-mode{
		&:before{
			background-color: orange;
		}
	}
	.esports{
		&:before{
			background-color: yellow;
		}
	}
	.subfeatures{
		margin: $unit * 4 0;
		border-radius: $unit;
		overflow: hidden;
		li{
			background-color: transparentize(white,.97);
			padding: $unit * 2  $unit * 3 !important;
			&:nth-child(odd){
				background-color: transparentize(white,.94);
			}
			&:first-child{
				border-top-width: 0px;
			}
		}
	}
}







@media only screen and (max-width: 1056px) {
	.timeline-section-sidebar{
		flex-basis: auto;
		padding-right: 0;
		transform: translateX(-$unit * 3);
		.text{
			display: none;
		}
	}
	.timeline-section{
		.path-anchor-mid{
			background-color: $brand;
		}
		.path-anchor-edge{
			background-color: white;
		}
		.path-anchor{
			transform: rotateZ(0deg)
		}
	}
	.timeline-section-entry{
		margin-top: $unit * 3;
		margin-bottom: $unit * 3;
		padding-top: $unit * 3;
		&:first-child{
			margin-top: $unit * 5;
		}
		&:last-child{
			margin-bottom: $unit * 5;
		}
		h1,h2,h3,h4,h5,h6,p,li{
			padding: 0 $unit * 3;
		}
	}
	.timeline-section-entry-features{
		li{
			background-color: transparent;
			padding: $unit * 2 $unit * 5;
			//padding-top: $unit;
			//padding-bottom: $unit;
			&:nth-child(odd){
				background-color: transparentize(white,.88);
			}
		}
	}
	.path{
		.path-top{
			height: $unit * 5;
		}
	}
}
