// Variables
@import '../_framework/variables/layout';
@import '../_framework/variables/colour';
@import '../_framework/variables/font';

// Mixins
@import '../_framework/mixins/animation';


footer{
	display: none;
	flex-grow: 0;
	flex-shrink: 0;
	padding: $unit * 5;
	border-top: 1px solid $light;
}

aside{
	@include resetFontSize;
	width: 100%;
	max-width: $sidebar-width;
	height: 100vh;
	min-height: 100%;
	overflow: hidden;
	overflow-y: scroll;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: auto;
	background-color: $darkest;
	
	display: flex;
	flex-direction: column;
	footer{
		display: block;
	}
}

#logo{
	flex-grow: 0;
	flex-shrink: 0;
	margin: $unit * 5 0;
	width: 100%;
	text-align: center;
	box-sizing: border-box;
	img{
		display: inline-block;
		margin: 0 $unit * 3;
	}
}

nav{
	flex-grow: 1;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	ul{
		flex-grow: 1;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between
	}
	li{
		flex-grow: 0;
		flex-shrink: 0;
		list-style: none;
		display: flex;
		position: relative;
		//width: 100%;
		z-index: 10;
		margin-bottom: -1px;
		//&:first-child{ flex-grow: 1}
		&:hover{
			z-index: 15;
		}
	}
	img{
		display: none;
		opacity: .6;
		margin: 0 auto;
	}
	a,span{
		width: 100%;
		display: block;
		padding: $unit * 3 $unit * 5;
		border: 1px solid transparent;
		border-left-width: 0;
		border-right-width: 0;
		//margin-bottom: -1px;
	}
	a:hover{
		background-color: transparentize($brand, .82);
		border-color: transparentize($brand, .6);
		img{
			opacity: 1;
		}
	}
	span{
		h5{
			color: $brand;
		}
	}
}


@media only screen and (max-width: 1056px) {
	footer{
		display: block;
	}
	
	aside{
		max-width: none;
		height: auto;
		min-height: none;
		
		overflow-y: hidden;
		
		position: relative;
		background-color: transparent;
		
		display: flex;
		flex-direction: column;
		footer{
			display: none;
		}
	}
	
	nav{
		h1,h2,h3,h4,h5,h6,p{
			margin-bottom: 0;
			display: block;
			text-align: center;
		}
		h5,h6{
			display: none;
		}
		ul{
			flex-direction: row;
			flex-wrap: wrap;
		}
		li{
			width: auto;
			flex-grow: 1;
		}
		img{
			display: block;
		}
		a,span{
			padding: $unit * 3 0;
		}
	}
	
	#logo{
		margin: $unit * 5 0 $unit * 2 0;
		img{
			width: 80px;
			height: 86px;
		}
	}
}
