// Colours
$brand: 							#F40000;
$brand-alt: 					#BD1717;
			
$lightest: 						#fff;
$light: 							rgb(60,60,60);
$dark: 							rgb(24,24,24);
$darkest: 						#000;



// System
$system-error:					$brand;
$system-info:					#0063E3;
$system-alert:					#E1B920;
$system-confirm:				#00C253;



// Links
$link-colour-before: 		$brand;
$link-colour-after: 			lighten($brand, 18);



// Typography
$text-colour-highlight: 	$brand;
$text-colour-light: 			$lightest;
$text-colour-dark:  			$dark;

$text-colour:  				$text-colour-light;



// Background
$background-dark: 			$dark;
$background-light: 			$lightest;

$background: 					$background-dark;

