// Variables
@import '../_framework/variables/layout';
@import '../_framework/variables/colour';
@import '../_framework/variables/font';

// Mixins
@import '../_framework/mixins/animation';

#background{
	background-image: url('src/background.jpg');
	background-size: cover;
	background-position: center;
	
	width: 100%;
	height: 100vh;
	
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	
	opacity: .24;
	
	&::after{
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 2;
		display: block;
		background-image: linear-gradient(0deg, $background 0%, transparentize($background, 1) 100%);
	}
}


#content-area{
	position: relative;
	width: $content-width;
	margin-left: $sidebar-width;
	#background{
		z-index: 1;
	}
	header,#timeline{
		position: relative;
		z-index: 50;
	}
}


header, #timeline{
	width: 100%;
	max-width: 1086px;
	display: block;
	margin: 0 auto;
	padding: $unit * 5;
}

header{
	margin-top: $unit * 20;
	h1,h2,h3,h4,h5,h6,p{
		margin-left: $sidebar-timeline-width;
		width: $content-timeline-width;
	}
}

//#timeline{ margin-bottom: $unit * 20;}


@media only screen and (max-width: 1056px) {
	#content-area{
		width: 100vw;
		margin-left: 0;
	}
	header{
		margin-top: $unit * 5;
		h1,h2,h3,h4,h5,h6,p{
			margin-left: 0;
			width: 100%;
		}
	}
	header, #timeline{
		padding: $unit * 3;
	}
}
